import { useEffect, useState } from "react";
import { useSnackbar } from "notistack";
import { useParams, useLocation, useNavigate } from "react-router-dom";
// @mui
import { Container } from "@mui/material";
// routes
import { PATH_DASHBOARD } from "../../routes/paths";
import { useDispatch } from "../../redux/store";
// hooks
import useSettings from "../../hooks/useSettings";
import { getUserCommission, getUserCommissions } from "../../redux/slices/usercommission";
// components
import Page from "../../components/Page";
import HeaderBreadcrumbs from "../../components/HeaderBreadcrumbs";
// sections
import UserCommissionNewEditForm from "../../sections/@dashboard/configuration/UserCommissionNewEditForm";
import extractErrorMessage from "../../utils/error";
import { useTranslation } from "react-i18next";
// ----------------------------------------------------------------------

export default function UserCommissionCreate() {
  const { themeStretch } = useSettings();

  const { t } = useTranslation();

  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { enqueueSnackbar } = useSnackbar();

  const { pathname } = useLocation();

  const { userCommissionKey = "" } = useParams();

  const isEdit = pathname.includes("edit");

  const [currentUserCommission, setCurrentUserCommission] = useState(null);

  useEffect(() => {
    if (isEdit) {
      handleGetObject();
    }
  }, [userCommissionKey]);

  const handleGetObject = async () => {
    try {
      const result = await getUserCommission(Number(userCommissionKey));
      setCurrentUserCommission(result);
      dispatch(getUserCommissions());
    } catch (error) {
      console.error(error);

      const errorMessage = extractErrorMessage(error);

      enqueueSnackbar(errorMessage, {
        variant: "error",
      });
      navigate(PATH_DASHBOARD.configuration.usercommission);
    }
  };

  return (
    <Page
      title={`Configuration: ${
        !isEdit ? t("p_usercommission.new_usercommission") : t("p_usercommission.edit_usercommission")
      }`}
    >
      <Container maxWidth={themeStretch ? false : "lg"}>
        <HeaderBreadcrumbs
          heading={!isEdit ? t("p_usercommission.new_usercommission") : t("p_usercommission.edit_usercommission")}
          links={[
            { name: t("params.dashboard"), href: PATH_DASHBOARD.root },
            {
              name: t("params.configuration"),
              href: PATH_DASHBOARD.configuration.root,
            },
            {
              name: !isEdit
                ? t("p_usercommission.new_usercommission")
                : `${t("p_usercommission.usercommission")} ${currentUserCommission?.type_transaction_name}`,
            },
          ]}
        />

        <UserCommissionNewEditForm isEdit={isEdit} currentUserCommission={currentUserCommission} />
      </Container>
    </Page>
  );
}
