import PropTypes from "prop-types";
import * as Yup from "yup";
import { useEffect, useMemo } from "react";
import { useSnackbar } from "notistack";
import { useNavigate } from "react-router-dom";
// form
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
// @mui
import { LoadingButton } from "@mui/lab";
import { Box, Card, Grid, Stack, Typography } from "@mui/material";
// utils
// routes
import { PATH_DASHBOARD } from "../../../routes/paths";
// components
import {
  FormProvider,
  RHFSelect,
  RHFTextField,
  RHFSwitch,
} from "../../../components/hook-form";
import { useDispatch, useSelector } from "../../../redux/store";
import {
  addUserCommission,
  updateUserCommission,
  addUserCommissionSuccess,
  updateUserCommissionSuccess,
  getOperators,
} from "../../../redux/slices/usercommission";
import extractErrorMessage from "../../../utils/error";
import { useTranslation } from "react-i18next";
import { getSupervisorsToSelect } from "src/redux/slices/supervisor";
// ----------------------------------------------------------------------

UserCommissionNewEditForm.propTypes = {
  isEdit: PropTypes.bool,
  currentUserCommission: PropTypes.object,
};

export default function UserCommissionNewEditForm({
  isEdit,
  currentUserCommission,
}) {
  const navigate = useNavigate();

  const { t } = useTranslation();

  const { enqueueSnackbar } = useSnackbar();

  const NewUserCommissionSchema = Yup.object().shape({
    user: Yup.string().required(
      t("field.required", {
        obj: t("field.user"),
      })
    ),
    operator: Yup.string().required(
      t("field.required", {
        obj: t("field.operator"),
      })
    ),
    commission: Yup.number().required(
      t("field.required", {
        obj: t("field.commission"),
      })
    ),
  });

  const defaultValues = useMemo(
    () => ({
      user: currentUserCommission?.user || "",
      operator: currentUserCommission?.operator || 0,
      commission: currentUserCommission?.commission || 0,
      is_active: currentUserCommission?.is_active || true,
      keyId: currentUserCommission?.id || -1,
    }),
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [currentUserCommission]
  );

  const methods = useForm({
    resolver: yupResolver(NewUserCommissionSchema),
    defaultValues,
  });

  const dispatch = useDispatch();

  const {
    reset,
    watch,
    control,
    setValue,
    handleSubmit,
    formState: { isSubmitting },
  } = methods;

  const values = watch();

  useEffect(() => {
    if (isEdit && currentUserCommission) {
      reset(defaultValues);
    }
    if (!isEdit) {
      reset(defaultValues);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isEdit, currentUserCommission]);

  const onSubmit = async () => {
    try {
      if (isEdit) {
        const results = await updateUserCommission(values);
        dispatch(updateUserCommissionSuccess(results));
      } else {
        const results = await addUserCommission(values);
        dispatch(addUserCommissionSuccess(results));
      }
      reset();
      enqueueSnackbar(!isEdit ? t("success.CREATE") : t("success.UPDATED"));
      navigate(PATH_DASHBOARD.configuration.usercommission);
    } catch (error) {
      console.error(error);

      const errorMessage = extractErrorMessage(error);

      enqueueSnackbar(t(`error.${errorMessage}`), {
        variant: "error",
      });
    }
  };

  const { selectsupervisors } = useSelector((state) => state.supervisor);
  const { operators } = useSelector((state) => state.usercommission);

  useEffect(() => {
    dispatch(getSupervisorsToSelect());
    dispatch(getOperators(1, 1000));
  }, [dispatch]);

  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>
        <Grid item xs={12} md={8}>
          <Card sx={{ p: 3 }}>
            <Box
              sx={{
                display: "grid",
                columnGap: 2,
                rowGap: 3,
                gridTemplateColumns: {
                  xs: "repeat(1, 1fr)",
                  sm: "repeat(2, 1fr)",
                },
              }}
            >
              <RHFSelect
                name="user"
                label={t("field.user")}
                placeholder={t("field.user")}
              >
                <option value="" />
                {selectsupervisors?.data?.map((option) => (
                  <option key={option.uid} value={option.uid}>
                    {option.full_name}
                  </option>
                ))}
              </RHFSelect>
              <RHFSelect
                name="operator"
                label={t("field.operator")}
                placeholder={t("field.operator")}
              >
                <option value="" />
                {operators?.data?.map((option) => (
                  <option key={option.id} value={option.id}>
                    {option.name}
                  </option>
                ))}
              </RHFSelect>

              <RHFTextField name="commission" label={t("field.commission")} />

              <RHFSwitch
                name="is_active"
                labelPlacement="start"
                label={
                  <>
                    <Typography variant="subtitle2" sx={{ mb: 0.5 }}>
                      Activate ?
                    </Typography>
                    <Typography
                      variant="body2"
                      sx={{ color: "text.secondary" }}
                    >
                      Enabling this will automatically activate the user
                      commission
                    </Typography>
                  </>
                }
                sx={{ mx: 0, width: 1, justifyContent: "space-between" }}
              />
            </Box>

            <Stack alignItems="flex-end" sx={{ mt: 3 }}>
              <LoadingButton
                type="submit"
                variant="contained"
                loading={isSubmitting}
              >
                {!isEdit
                  ? t("params.create", {
                      obj: t("p_usercommission.usercommission"),
                    })
                  : t("params.save_change")}
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
